    getStates(country: string[]): { "id": string, "text": string }[] {
        return this.getAllStates()
            .filter(loc =>
                (country.length ? country.indexOf(loc.country) >= 0 : true))
            .map(state => {
                return {
                    id: state.id,
                    text: state.text
                }
            })
    }

    getCurrencies(): { "id": string, "text": string }[] {
        return this.getAllCurrencies()
            .map(curr => {
                return {
                    id: curr.id,
                    text: curr.description + " (" + curr.name + ")"
                }
            })
    }

    getSponsors(): { "id": string, "text": string }[] {
        return this.getAllFundingOrganizations()
            .map(org => {
                return {
                    id: org.id,
                    text: org.name
                }
            })
    }

    getCsoAreas(category: string): { "id": string, "text": string }[] {
        return this.getAllCsoResearchAreas()
            .filter(cso => (category ? cso.category === category : true))
            .map(cso => {
                return {
                    id: cso.id,
                    text: cso.name
                }
            })
    }

    
